import React, { Component } from 'react';
class Product extends Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }


  onProductRemove = (p) => {
    this.props.onProductRemove(p);
  }

  increaseQuantity = (product) => {
    if (product.quantity < 5) {
      product.quantity += 1;
      this.props.replaceProduct(product)
    }
  }

  decreaseQuantity = (product) => {
    if (product.quantity > 1) {
      product.quantity -= 1;
      this.props.replaceProduct(product)
    }
  }


  render() {
    let { products } = this.props;

    return (
      <table className="table table-condensed table-striped table-primary ">
        <thead>
          <tr>
            <th>Title</th>
            <th className="text-center">Quantity</th>
            <th className="text-center">Price</th>
            <th className="text-center" style={{ width: "100px" }}>Actions</th>
          </tr>
        </thead>

        <tbody id="cart_body">
          {
            products.map((product, index) => {
              return <tr key={index}>
                <td className="center"><a href="#">{product?.title}</a></td>
                <td>
                  <div className="center" style={{ position: "relative" }}>
                    <span onClick={() => this.increaseQuantity(product)}> <i className="fa fa-plus"></i> </span>
                    <input type="text" className="form-control" style={{ width: "50px", margin: "0px auto" }} value={product?.quantity} readonly />
                    <span onClick={() => this.decreaseQuantity(product)} > <i className="fa fa-minus" ></i></span>
                  </div>
                </td>
                <td className="text-center">{product?.currency} {product?.price}</td>
                <td className="center">
                  <button
                    onClick={() => this.onProductRemove(product)}
                    className="btn btn-danger center remove-item" ><i className="fa fa-times" disabled ></i></button>
                </td>
              </tr>

            })
          }
        </tbody>
      </table>

    );
  }
}
export default Product;