import React, { Component } from 'react';
import Product from './Product';
import _ from 'lodash';

class Cart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      sum: 0,
      currency: "USD"
    }
  }
  componentDidMount(){
    this.countSum();
  }

  onProductRemove = (product) => {
    let { products } = this.state;
    products = _.reject(products, (p) => {
      return p.id == product.id;
    });
    this.setState({ products }, () => {
      if(this.state.products.length === 0){
        this.props.onCartEmpty();
      }
      this.countSum();
    } );

  }

  addProduct = (p) => {
    let { products } = this.state;
    var product = _.find(products, {id: p.id});
    if (product){
      product.quantity +=1;
      this.replaceProduct(product);
    }
    else{
      p.quantity = 1;
      let currency = p.currency;
      products.push(p)
      this.setState({ products, currency }, () =>  this.countSum() );

    }
  }


  countSum = () => {
    let sum = 0;
    let { products } = this.state;
    _.each(products, (product) => {
      sum += (product.price * product.quantity);
    });
    this.setState({sum});
  }

  replaceProduct = (product) => {
    let { products } = this.state;
    var index = _.findIndex(products, { id: product.id });
    products.splice(index, 1, product);
    this.setState({ products });
    this.countSum();
  }

  render() {
    let { products } = this.state;
    let color = this.props.contaktiChat.color;
    let text_color = this.props.contaktiChat.text_color;
    return (
      <div id="contakti_cart_box" className="cart_box">
        <div className="msg_head" style={{ backgroundColor: color }}>

          <span id="contakti-user-name" style={{ color: text_color }}> Shopping Cart</span>
        </div>
        <div>
        </div>
        <div className="msg_wrap">
          <div className="cart_body" style={{ right: "15px" }}>
            <Product products={products} onProductRemove={this.onProductRemove} replaceProduct={this.replaceProduct} />

            <div className="">
              <span id="total_amount"> Sum: <strong> {this.state.currency} {this.state.sum}</strong> </span>
            </div>
            <br />
            <div>

              <label > PAYMENT METHOD:</label>

              <div className="center" id="payment_method_select">
                <select style={{ height: "25px", fontSize: "15px", width: "50%" }}>
                  <option> Card</option>
                  <option> PayPal</option>
                </select>
              </div>
              <br />

              <label > SHIPPING METHOD:</label>

              <div className="center" id="payment_method_select">
                <select style={{ height: "25px", fontSize: "15px", width: "50%" }}>
                  <option> ABC</option>
                  <option> DEF</option>
                </select>
              </div>

            </div>

            <br />
            <div className="center">
              <button style={{ color: text_color, backgroundColor: color }}> CHECKOUT</button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Cart;